@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@700&display=swap);
.app {
  width: 100%;
  height: 100%;
}

.header {
  z-index: 99;
  width: 100%;
  position: fixed;
  text-transform: capitalize;
  background-color: var(--color-main-2);
}

.header-container {
  display: flex;
  justify-content: flex-end;
}

.main-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem 2rem 1rem 1rem;
  color: var(--color-main-1);
}

.main-nav-list {
  display: flex;
  justify-content: center;
  align-items: center;
}

.main-nav-item {
  padding: 0.5rem;
  cursor: pointer;
}

.main-nav-item:hover {
  color: white;
  transform: scale(1.05);
}

@media only screen and (max-width: 700px) {
  .main-nav {
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: right;
    align-items: flex-end;
    text-align: right;
  }

  .mobile-menu {
    margin: 1rem 2rem 1rem 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    padding: 5px;
    cursor: pointer;
  }

  .mobile-menu:hover {
    transform: scale(1.1, 1.1);
    transition: ease-in;
  }

  .mobile-menu-bar {
    display: inline-block;
    margin: 4px 0;
    width: 100%;
    height: 15%;
    background-color: white;
  }

  .main-nav-list {
    z-index: 98;
    background-color: var(--color-main-2);
    padding: 1rem 2rem 1rem 1rem;
    transform: translateY(-400px);
    position: absolute;
  }

  .main-nav-list.active {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    transition: ease-in;
    transform: translateY(60px);
    position: absolute;
    width: 100vw;
  }
}

.footer {
  margin-top: 1rem;
  background-color: var(--color-secondary-2);
}

.footer-container {
  padding: 0.8rem;
  display: flex;
  justify-content: center;
}

.footer-sub-container {
  padding: 0 2rem;
}

.footer-container h3 {
  padding-bottom: 0.5rem;
}

.footer-links {
}

.footer-links li {
  padding: 0.2rem 0;
}

.footer-link a span {
  padding: 0 0.2rem;
}

@media only screen and (max-width: 700px) {
  .footer-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .footer-container h3 {
    padding-bottom: 0.5rem;
  }

  .footer-sub-container {
    max-width: 100%;
    padding: 0.5rem;
    text-align: center;
    overflow-wrap: break-word;
  }

  .footer-links li a {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 1rem;
  }

  #footer-links-header {
    display: none;
  }

  .footer-links {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
  }

  .footer-link a span {
    padding: 0 0.5rem;
  }
}

.landing {
  width: auto;
  height: 100vh;
  background: var(--color-main-1);
  background: url(/static/media/mountains.9330848c.jpg) no-repeat center;
  background-size: auto 100vh;
}

.landing-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-transform: capitalize;
  color: white;
}

.landing-container h1 {
  text-align: center;
  font-size: 5rem;
}

.tagline {
  display: flex;
  justify-content: center;
  align-items: center;
  font-style: italic;
}

.tagline > * {
  padding: 0 0.5rem;
}

.landing-container h2 {
}

@media only screen and (max-width: 700px) {
  .landing-text {
    font-size: 2vw;
  }

  .landing-container h1 {
    font-size: 5em;
  }

  .tagline {
    font-size: 1.5em;
  }
}

#intro-section {
}

#me-portrait {
  width: 300px;
  height: auto;
  /* border-radius: 50%; */
  /* float: left; */
  padding: 0rem 1rem 1rem 1rem;
  margin-right: 0.5rem;
}

.intro-blurb {
  color: var(--color-secondary-2);
  padding-bottom: 2rem;
  font-size: 1.2rem;
}

.intro-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

@media only screen and (max-width: 900px) {
  .intro-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .intro-paragraphs-container {
    margin-top: .5rem;
  }
}

.projects-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.project-tile {
  width: 40%;
  height: auto;
  margin: 1rem;
  background-color: var(--color-secondary-2);
  opacity: 0.8;
  overflow: hidden;
}

.project-tile:hover {
  opacity: 1;
}

.project-tile-container {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.project-img {
  width: 100%;
  height: 50%;
  background-color: var(--color-secondary-1);
}

.project-img img {
  max-width: 100%;
  max-height: 100%;
  padding: 15px;
}

.project-info {
  padding: 0.5rem 0.8rem;
  overflow-wrap: break-word;
  height: 50%;
}

.project-title {
  font-size: 1.2rem;
  text-align: left;
}

.project-description {
  padding-top: 0.5rem;
  color: var(--color-main-1);
  padding-bottom: 0;
}

@media only screen and (max-width: 700px) {
  .projects-container {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }

  .project-tile {
    width: 100%;
    margin: 1.5rem 0;
    height: auto;
    background-color: var(--color-secondary-2);
    overflow: auto;
  }

  .project-title {
    text-align: left;
  }

  .project-img {
    width: 100%;
    height: 50%;
    background-color: var(--color-secondary-1);
  }

  .project-img img {
    max-width: 100%;
    max-height: 100%;
    padding: 15px;
  }
}

:root {
  /* Define color themes */
  --color-main-1: rgb(190, 177, 170);
  --color-main-2: rgb(58, 78, 83);
  --color-secondary-1: rgb(48, 59, 57);
  --color-secondary-2: rgb(98, 86, 83);
}

* {
  box-sizing: border-box;
  margin: 0;
  border: 0;
  font-family: "Noto Sans", sans-serif;
}

html {
  width: 100%;
  height: 100%;
}

body {
  width: 100%;
  height: 100%;
  background-color: rgb(190, 177, 170);
  background-color: var(--color-main-1);
  -ms-overflow-style: none;
  scrollbar-width: none;
}

#root {
  width: 100%;
  height: 100%;
}

*::-webkit-scrollbar {
  display: none;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  padding: 0;
}

h1,
h2,
h3 {
  text-transform: capitalize;
}

.section {
  width: 100%;
}

.section-container {
  background-color: rgb(190, 177, 170);
  background-color: var(--color-main-1);
  width: 80%;
  max-width: 900px;
  margin: 0 auto;
  padding: 2rem 0 1rem 0;
  overflow: auto;
}

.section-container h1,
.section-container h2 {
  color: rgb(98, 86, 83);
  color: var(--color-secondary-2);
  font-size: 3rem;
  padding: 1rem 0 2rem 0;
  text-align: center;
}

